<template>
	<div>
		<Row class="nav-wrapper">
			<ul class="nav-list">
				<li class="nav-item">
					欢迎您, {{ username }}
				</li>
				<li class="nav-item">
					<a href="javascript:;" @click="logout">退出</a>
				</li>
			</ul>
		</Row>
		<Row class="home-wrapper">
			<Col span="4" style="height: 100%;">
				<Menu theme="light">
					<div v-for="(menu, index) in menus" :key="menu.id">
						<Submenu :name="index" v-if="menu.children && menu.children.length">
		                    <template slot="title">
		                        {{menu.name}}
		                    </template>
		                    <MenuItem :name="`${index}-${subindex}`" v-for="(submenu, subindex) in menu.children" :key="submenu.id">
		                    	<router-link :to="{name: submenu.route}">{{submenu.name}}</router-link>
		                    </MenuItem>
		                </Submenu>
		                <MenuItem :name="index" v-else>
							<router-link :to="{name: menu.route}">{{menu.name}}</router-link>
		                </MenuItem>
					</div>
				</Menu>
			</Col>
			<Col span="20">
				<!-- 
				{{menus | json}} -->

				<router-view />
			</Col>
		</Row>
	</div>
</template>

<script>
	import AppModel from '../model/app'
	import Bus from '../bus'
	import axiosInstance from '@/api'
	const appInstance = new AppModel()

	export default {
		data () {
			return {
				menus: Bus.menuTreeRoutes
			}
		},
		mounted() {
		},
		methods: {
			logout() {
				appInstance.handleSession()
				axiosInstance.interceptors.request.eject(Bus.myInterceptor)
				this.$router.push({name: 'login'})
			}
		},
		computed: {
			username(){
				return Bus.username
			}
		}
	}
</script>

<style>
	.nav-wrapper{
		position: fixed;
		left: 0;
		top: 0;
		right: 0;
		height: 40px;
		overflow: hidden;
		background-color: #2d8cf0;
		color: #fff;
		padding: 0 20px;
	}
	.nav-list{
		height: 100%;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}
	.nav-item{
		margin-right: 20px;
	}
	.nav-item a{
		color: #fff;
	}
	.home-wrapper{
		position: fixed;
		left: 0;
		top: 40px;
		bottom: 0;
		right: 0;
	}
	.home-wrapper .ivu-menu{
		height: 100%;
	} 
	.ivu-menu-item a{
		color: #333;
		display: inline-block;
		width: 100%;
		height: 100%;
	}
	.router-link-exact-active.router-link-active{
		color: #2d8cf0;
	}
</style>


